.container-img {
    width: 50%;
    padding-top: 25vh;
    position: relative;
    min-height: 70vh;
}

.img-academics {
    margin-left: 30px;
    width: 100%;
}

.img-eter {
    transform: rotate(2deg);
    position: absolute;
    opacity: 0.4;
}

.img-nassau {
    transform: rotate(-18deg);
    position: absolute;
    opacity: 0.4;
}

.img-ufcg {
    transform: rotate(-10deg);
    position: absolute;
    opacity: 0.4;
}

.img-trybe {
    transform: rotate(11deg);
    position: absolute;
    opacity: 0.4;
}


.img-orbitall {
    transform: rotate(-6deg);
    position: absolute;
    opacity: 0.4;
}

.img-aec {
    transform: rotate(11deg);
    position: absolute;
    opacity: 0.4;
}

.img-selected {
    border: 7px solid white;
    z-index: 2;
    opacity: 1;
    transition: opacity 1s;
}