.home {
    display: flex;
    flex-direction: column;
}

.section-content {
    width: 50%;
    padding: 0 20px;
    z-index: 1;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
}

.title-text {
    display: flex;
    align-items: flex-end;
    font-size: 3vw;
    text-align: left;
    width: 100%;
    padding-bottom: 10px;
}

.paragraph-text {
    margin-bottom: 0;
    line-height: 1.6;
    font-size: 1.3vw;
}

.aside-image {
    margin: 20px;
    width: 40%;
    z-index: 1;
}

.img-background-me {
    display: none;
}

.img-aside {
    width: 90%;
    border-radius: 50%;
    margin-right: 40px;
}

.btn-arrow-down {
    margin-top: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    z-index: 1;
    position: relative;
    background-color: transparent;
    border: none;
}

@media screen and (max-width: 910px) {
    main {
        flex-direction: row;
        position: relative;
    }

    .section-content {
        width: 100%;
        padding: 0;
        height: 500px;
    }

    .aside-image {
        display: none;
    }

    .img-aside {
        height: 60vh;
        width: 60vh;
        opacity: 0.5;
    }

    .img-background-me {
        border-radius: 50% 0 0 50%;
        position: absolute;
        display: flex;
        left: 55%;
        width: 45%;
        height: 100%;
    }

    .section-content>div {
        position: absolute;
        z-index: 1;
        width: 100%;
    }

    .title-text {
        height: 30vh;
        display: flex;
        align-items: flex-end;
        justify-content: center;
        font-size: 25px;
    }

    article {
        height: 70vh;
        width: 70%;
        text-align: center;
    }

    .paragraph-text {
        font-size: 18px;
    }
}